.app {
  display: grid;
  grid-template-columns: 1fr 5fr;
  height: 100vh;
}

.nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
  height: 100vh;
  box-shadow: 0 10px 100px rgba(0, 0, 0, 0.5);
}
.logo {
  background-image: url(./components/nav/Snimok_ekrana_2020-01-07_v_17_00_45.png);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100px;
}
.Shoartcut {
  display: flex;
  flex-direction: column;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.26s ease;
}
.Shoartcut a {
  text-decoration: none;
  text-align: center;
  font-size: 2rem;
  margin-top: 40px;
  color: black;
}

.navanimation {
  transform: scaleY(1);
}

.choosingmenu {
  background-color: red;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.choosebuttom {
  margin: 10px;
  position: relative;
  outline: none;
  text-decoration: none;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  text-transform: uppercase;
  height: 60px;
  width: 210px;
  opacity: 1;
  background-color: #ffffff;
  border: 1px solid rgba(22, 76, 167, 0.6);
}
.choosebuttom:hover {
  animation: rotate 0.7s ease-in-out both;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
  25% {
    transform: rotate(3deg) translate3d(0, 0, 0);
  }
  50% {
    transform: rotate(-3deg) translate3d(0, 0, 0);
  }
  75% {
    transform: rotate(1deg) translate3d(0, 0, 0);
  }
  100% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
}
